@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

:root{
    --white:#FFFFFF;
    --black:#000000;
    --lightgrey:#D1D9E0;
    --darkgray:#59636E;
    --blue:#4883DB;
}
/* Outer background */
.main-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: linear-gradient(to bottom right, #FFFFFF, #F3FCF1);
    min-height: 100vh;
    padding-bottom: 10px;
    /* transform: translateY(-40px); */
}

/* Inner container styling */
.form-container {
  min-height: 40vh;
  margin: 20px auto 0 auto;
  padding: 55px 60px 35px 45px;
  background: var(--white);
  border-radius: 10px;
  text-align: center;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
}
@media (max-width: 600px) {
  .form-container {
    width: 370px; /* 1rem = 14px */
  }
}

/* Logo styling */
.logo{
    margin: 0;
}

.subtitle {
  font-family: 'Montserrat', sans-serif; /* Keep the font consistent */
  font-size: 24px; /* Slightly larger font for emphasis */
  font-weight: 600; /* Medium weight for balance */
  color: var(--darkgray); /* Use the existing dark gray for subtle contrast */
  text-align: center; /* Center-align for symmetry */
  margin-top: 15px; /* Adequate spacing above */
  margin-bottom: 25px; /* Spacing below for separation */
  letter-spacing: 0.5px; /* Slight spacing for readability */
  margin: 3px
}

/* Title */
.title {
    font-size: 1.2rem;
    margin-bottom: 20px;
    color: #555555;
}

label {
    width: 100%;
    max-width: 95%;
    margin: 0 auto 0px auto;
    display: block;
    text-align: left;
    margin-bottom: 5px;
    color: #333333;
    font-family: Switzer, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    font-size: 0.70rem;
    font-weight: 500;
}

.input-box {
    width: 80%;
    max-width: 380px;
    padding: 10px;
    border: none;
    border-bottom: 1px solid var(--lightgrey);
    margin-bottom: 15px;
    font-size: 0.8rem;
    padding-right: 40px; 
}

.input-box:focus-visible{
    outline: none;
}
.continue-btn {
    padding: 6px;
    width: 100%;
    display: block;
    background-color: var(--blue);
    color: var(--white);
    border: none;
    font-size: 20px;
    cursor: pointer;
    border-radius: 10px;
}

.continue-btn:hover {
    background-color: var(--black);
}

/* Terms text */
.terms {
    font-size: 10px;
    color: var(--darkgray);
    padding: 14px 0;
    margin: 0;
    border-bottom: 1px solid var(--lightgrey);
}

