input::placeholder {
    letter-spacing: 1.2px;
}

.login-box-content{
  width:100%;
  max-width: 380px;
  margin: 0 auto;
}

.login-options{
  display: flex;
  flex-direction: column;
  align-items: center;    
  justify-content: center;
}
/* Buttons */
.login-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  max-width: 353px;
  background-color: var(--white);
  color: var(--blue);
  padding: 10px 0px;
  /* font-size: 24px; */
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid var(--lightgrey);
}
.login-btn svg{
  padding-right: 28px;
}
.login-btn a{
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  color: var(--blue);
}
.login-btn::before{
    content: "";
    position: absolute;
}
.login-btn a:hover{
  color: var(--darkgray);
}
.github-btn{
  margin: 30px 0 0 0;
}
.github-btn svg{
  fill: var(--black);
}
/* Links and Policies */
.policy-text {
  font-size: 10px;
  color: var(--black);
  margin: 0;
  padding: 14px 0;
  border-bottom: 1px solid var(--lightgrey);
}
.policy-text a{
  text-decoration: none;
  color: var(--blue);
}
.policy-text a{
  text-decoration: underline;
}
.signup-text{
  font-size: 12px;
  color: var(--darkgray);
  margin: 30px 0 0 0;
}
.signup-text a{
  font-size: 18px;
  color: var(--blue);
  text-decoration: none;
}
.signup-text a:hover {
  text-decoration: underline;
}
.signup-text span{
  font-size: 18px;
  color: var(--darkgray);
}

.password-container {
  position: relative;
  width: 100%;
}

.toggle-password-btn {
  position: absolute;
  top: 42%;
  right:0px;
  transform: translateY(-50%);
  background: none;
  border: none;
  cursor: pointer;
  color: #c3c3c3; /* Change to your preferred color */
  font-size: 1.2rem; /* Adjust size of the icon */
  outline: none;
}

.toggle-password-btn:hover {
  color: #000; /* Optional hover effect */
}

.error-message {
  color: #ff4d4f; /* Bright red for visibility */
  background-color: #ffecec; /* Light red background for emphasis */
  border: 1px solid #ff4d4f; /* Red border for clarity */
  padding: 10px; /* Add some padding for spacing */
  margin-top: 15px; /* Space above the message */
  border-radius: 5px; /* Rounded corners for modern look */
  font-size: 0.95rem; /* Slightly smaller font */
  text-align: center; /* Center align the message */
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
  max-width: 350px;
}
.success-message {
  color: hsl(120, 100%, 30%); /* Dark green text */
  background-color: hsl(120, 100%, 90%); /* Light green background */
  border: 1px solid hsl(120, 100%, 40%); /* Medium green border */
  padding: 10px; /* Add some padding for spacing */
  margin-top: 15px; /* Space above the message */
  border-radius: 5px; /* Rounded corners for modern look */
  font-size: 0.95rem; /* Slightly smaller font */
  text-align: center; /* Center align the message */
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
}
